import Engine from './utils/engine';
// import Engine from './utils/engine3d';
import anime from 'animejs/lib/anime.es.js';

import { loadFont } from './utils/font';

import GothamFont from '../data/Gotham-Bold.ttf';

const WIDTH = 480;
const HEIGHT = 480;

let FontObj;
let path;
let animPath;

const difference = 100;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

app.onRender(({ frame, width, height, debug }) => {
  ctx.save();
  ctx.clearRect(0, 0, width, height);

  if (path) {

    ctx.beginPath();
    ctx.arc(width / 2, height / 2, width / 4, 0, Math.PI * 2);
    ctx.strokeStyle = 'orange';
    ctx.stroke();

    ctx.save();
    ctx.translate(
      ((width - path.boundingBox.width) / 2) + (difference / 2),
      (height / 2) + (path.boundingBox.height / 2) - (difference / 2),
    );

    // ctx.strokeRect(
    //   path.boundingBox.x1,
    //   path.boundingBox.y1,
    //   path.boundingBox.width,
    //   path.boundingBox.height,
    // )

    let c = 0;
    ctx.beginPath();
    for (let i = 0; i < path.points.length; i++) {
      for (let p = 0; p < path.points[i].length; p++) {
        // const { x, y } = path.points[i][p];

        if (p == 0) {
          ctx.moveTo(animPath[c].x, animPath[c].y);
        } else {
          ctx.lineTo(animPath[c].x, animPath[c].y);
        }
        c++;
      }
      ctx.closePath();
    }

    ctx.strokeStyle = 'blue';
    ctx.stroke();
    ctx.restore();

    // if (path.fill) {
    //     ctx.fillStyle = path.fill;
    //     ctx.fill();
    // }

    // if (path.stroke) {
    //     ctx.strokeStyle = path.stroke;
    //     ctx.lineWidth = path.strokeWidth;
    //     ctx.stroke();
    // }

  }

  ctx.restore();
});

loadFont(GothamFont)
  .then(Font => {
    FontObj = Font;
    path = FontObj.getOptimisedPath('MELT', 120, { fidelity: 30 });

    animPath = path.points.flat().map(p => {
      return {
        x: p.x,
        y: p.y,
        origX: p.x
      }
    });

    anime({
      targets: animPath,
      x: {
        value: `-=${difference}`
      },
      y: {
        value: `+=${difference}`
      },
      duration: 1000,
      delay: (el, i) => {
        let perc = (el.origX - path.boundingBox.x1) / path.boundingBox.width;

        return perc * 1000;
      },
      endDelay: 500,
      direction: 'alternate',
      loop: true,
      easing: 'easeInOutQuint'
    })

    app.start();
  });
